import styled from "styled-components";

export const DonationContainer = styled("div")`
  margin-top: 5rem;
  padding: 5rem 0;
  background: repeating-linear-gradient(
  145deg,
  #e4ebf7,
  #ffffff 2px,
  #ffffff 20px,
  #ffffff 15px
  );

  @media only screen and (max-width: 1024px) {
    padding: 3rem 0;
  }
`

export const DivWithLeftMargin = styled("div")`
  margin-left: 1rem;
`